import * as React from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import {graphql, Link} from 'gatsby'
import ResearchList from '../components/layout-researchList'
import {
  page, container, about, photo, photoImage, intro, myName, bio, links, footer, sectionTitleRow, research
} from '../components/layout.module.css'

const IndexPage = ({data}) => {

  return (
    <div className={page}>
      <div className={container}>
        <div className={about}>
          <div className={photo}>
                 <StaticImage
                   className={photoImage}
                   alt="Clifford, a reddish-brown pitbull, dozing in a bean bag chair"
                   src="../images/photo.png"
                 />
          </div>
          <div className={intro}>
            <h2 className={myName}>
              Jianzhe Gu
            </h2>
            <div className={bio}>
              <b>Jianzhe</b> <i>[dʒendʒər]</i> is a 4th-year Ph.D. in the <a href="https://www.hcii.cmu.edu/">Human-Computer Interaction Institute</a> @
              <a href="https://www.cmu.edu/">Carnegie Mellon University</a>, advised by <a href="https://www.morphingmatter.cs.cmu.edu/team/lining-yao">Lining Yao</a>. His research interest focuses on computational design, digital fabrication, human-computer interaction and computer graphics. Blending digits and materials, he devotes himself to exploring the future form of shape-changing interfaces that can be computationally designed, evolved, and generated. His research work was published at premier conferences of HCI, including CHI and UIST. Prior to joining the CMU, Jianzhe earned his bachelor’s degree in Electrical and Computer Engineering from Shanghai Jiao Tong University, China.
            </div>
            <div className={links}>
              <Link to={"https://scholar.google.com/citations?user=ETjqnDsAAAAJ&hl=en"}>Google Scholar</Link>
              <Link to={"https://github.com/riceroll/"}>Github</Link>
              <Link to={"../src/images/photo.png"}>CV</Link>
            </div>
          </div>
        </div>


        <div className={research}>

          <div className={sectionTitleRow}>
            <p>Research</p>
          </div>

          <ResearchList data={data}/>

        </div>

        <div className={footer}>
          Updated on March 3rd 2022

        </div>
      </div>
    </div>
  )
}


export const query = graphql`
  {
  allMdx(sort: {fields: fileAbsolutePath, order: ASC}) {
    nodes {
      body
      frontmatter {
        myimage {
          childImageSharp {
            gatsbyImageData
          }
        }
        pdf
        doi
        project
        code
        video
        title
      }
    }
  }
}



`


export default IndexPage
