import * as React from 'react'
import { graphql, Link } from 'gatsby'
import { MDXRenderer } from 'gatsby-plugin-mdx'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import StaticImage  from 'gatsby-plugin-image'
import {
  project, projectImage, projectInfo, projectTitle, projectAbstract, projectLinks
} from '../components/layout.module.css'

const ResearchList = ({ data }) => {

  return (
    data.allMdx.nodes.map( node => (

        <div className={project} key={node.id}>
          <div className={projectImage}>

            <GatsbyImage alt={'hehe'} image={getImage(node.frontmatter.myimage)} style={{'border-radius': '5%', 'border': '2px solid'}}/>

          </div>

          <div className={projectInfo}>
            <h2 className={projectTitle}>
              {node.frontmatter.title}
            </h2>

            <div className={projectAbstract}>
              <MDXRenderer>
                {node.body}
              </MDXRenderer>
            </div>

            <div className={projectLinks}>
              {node.frontmatter.project?
                <Link to={node.frontmatter.project}>[PROJECT]</Link>
                : null}
              {node.frontmatter.pdf?
                <Link to={node.frontmatter.pdf}>[PDF]</Link>
                : null}
              {node.frontmatter.doi?
                <Link to={node.frontmatter.doi}>[DOI]</Link>
                : null}

              {node.frontmatter.video?
                <Link to={node.frontmatter.video}>[VIDEO]</Link>
                : null}

              {node.frontmatter.code?
                <Link to={node.frontmatter.code}>[CODE]</Link>
                : null}

            </div>
          </div>

        </div>
      )
    )
  )
}

export default ResearchList